<script>
	var selected;

	function _focusFig(e) {
		let el = e 
		if (e.currentTarget) {
			e.cancelBubble = true;
			e.preventDefault();
			el = e.currentTarget
		}
		let old = el.parentNode.querySelectorAll('img')
		if (old) {
			for (let o of old) {
			o.style.outline = ''
			}
		}
		el.querySelector('img').style.outline = 'dashed 4px white'
		selected = el;
	}

	function _blurFig(ev) {
		ev.cancelBubble = true;
		ev.preventDefault();
		let old = ev.target.querySelectorAll('img')
		for (let o of old) o.style.outline = ''
		selected = null
	}

	function _moveTxt(e) {
		let el = e.target
		if (selected && selected.tagName == 'FIGURE') {
			let old = selected.querySelector('figcaption');
			if (old) document.getElementById('as').appendChild(old);
			if (old != el) {
				selected.appendChild(el);
				if (selected.nextElementSibling) _focusFig(selected.nextElementSibling)
				else _focusFig(selected.parentNode.firstElementChild)
			}
		} else {
			//document.getElementById('dragdrop').appendChild(el);
		}
	}

	function _dragover(e) {
		e.preventDefault();
console.log('_dragover',e.dataTransfer.getData("text"))
		if (e.dataTransfer.getData("text") || true) {
			let el = e.target
			if (el.tagName == 'FIGURE') /* console.log(e); */_focusFig(el)
			if (el.parentNode.tagName == 'FIGURE') _focusFig(el.parentNode)
		}
	}
	
	function _dragstart(e) {
		e.dataTransfer.setData("text", e.target.id);
		e.dataTransfer.dropEffect = "copy";
		console.log('_dragstart',e.dataTransfer.getData("text"))

	}
	
	function _drop(e) {
		e.preventDefault();
		let el = document.getElementById(e.dataTransfer.getData("text"));
		if (el && el.tagName == 'FIGCAPTION') {
			let selected = e.target.parentNode;
			if (selected.tagName == 'FIGURE') {
				let old = selected.querySelector('figcaption');
				if (old) document.getElementById('as').appendChild(old);
				selected.appendChild(el);
			} else {
				//document.getElementById('dragdrop').appendChild(el);
			}
		}
	}
</script>

<header>
	<h1>Quizzone experiment</h1>
	<p>Nem vagyunk "bezárva" egy képernyőbe, mint a projektoron! Scrollozhatunk! Drag & drop! Select & pair!</p>
</header>

<main id="questions">
	<div class="parosito" id="dragdrop" on:drop="{_drop}" on:dragstart="{_dragstart}" on:dragover="{_dragover}">
		<div id="qs" on:click|self="{_blurFig}">
			<figure class="fig" on:click={_focusFig}>
				<img src="/images/city.jpeg" alt="1"><span>1</span></figure>
			<figure class="fig" on:click={_focusFig}>
				<img src="/images/transport.jpeg" alt="2"><span>2</span>
			</figure>
			<figure class="fig" on:click={_focusFig}>
				<img src="/images/animals.jpeg" alt="3"><span>3</span>
			</figure>
			<figure class="fig" on:click={_focusFig}>
				<img src="/images/nature.jpeg" alt="4"><span>4</span>
			</figure>
			<figure class="fig" on:click={_focusFig}>
				<img src="/images/people.jpeg" alt="5"><span>5</span>
			</figure>
		</div>
		<div id="as" class="as">
			<figcaption draggable="true" class="txt" id="txt-1" on:click="{_moveTxt}">A) PEOPLE</figcaption>
			<figcaption draggable="true" class="txt" id="txt-2" on:click="{_moveTxt}">B) NATURE</figcaption>
			<figcaption draggable="true" class="txt" id="txt-3" on:click="{_moveTxt}">C) ANIMALS</figcaption>
			<figcaption draggable="true" class="txt" id="txt-4" on:click="{_moveTxt}">D) TRANSPORT</figcaption>
			<figcaption draggable="true" class="txt" id="txt-5" on:click="{_moveTxt}">E) CITY</figcaption>
		</div>
	</div>
</main>

<footer>
	<h6>2020. január</h6>
</footer>

<style>
	#qs, #as {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-evenly;
	}
	#qs {
		width: 100%;
		padding: 2rem 0;
		counter-reset: qnum;
	}
	#as {
		position: sticky;
		bottom: 1rem;
		width: 100%;
		background-color: rgba(105, 105, 105, .75);/* dimgray */
		/* padding: 1rem 0;
		border-top: dashed 4px white; */
	}


	.parosito .fig {
		position: relative;
		margin: 1%;
		width: 31%;
		/* height: 0;
		padding-top: 75%;
		overflow: hidden; */
	}
	.parosito .fig span {
		display: none;
		/* position: absolute;
		left: -.5rem;
		top: 1rem;
		outline: solid 2px white;
		width: 2rem;
		text-align: center;
		background: orange;
		color: white; */
	}
	.parosito .fig::after {
		counter-increment: qnum;
		content: counter(qnum);
		position: absolute;
		left: -.25rem;
		top: 1rem;
		outline: solid 2px white;
		width: 2rem;
		text-align: center;
		background: orange;
		color: white;
	}

	.parosito .txt {
		flex-grow: 1;
		box-sizing: border-box;
		max-width: 31%;
		margin: 2% 1%;
		padding: 0 .5rem;
		outline: solid 2px white;
		text-align: center;
		background: orange;
		color: white;
	}

	@media screen and (orientation:portrait) {
		.parosito .fig {
			margin: 1%;
			width: 46%;
		}
		.parosito .txt {
			margin: 1%;
			max-width: 46%;
			margin: .5rem 0 0;
		}
	}

	/* párosítás után */
	.parosito .fig .txt {
		position: absolute;
		margin: 0 -.25rem;
		width: fit-content;
		max-width: unset;
		bottom: 1rem;
	}

</style>
